import * as dat from 'dat.gui';
import {Stage} from './Stage.js';
import * as utils from './utils.js';

/**
	       dat.GUI   . dat.GUI  ,       :  , , .
*/
export class GuiManager {
	
	/**
		,     -   Stage
	*/
	constructor(stage) {
		this.stage = stage;
		this.gui = new dat.GUI({autoplace: true});
		// this.gui.domElement = document.querySelector('#controls');
		this.listeners = [];
		this.setupOptions();
	}
	
	/**
		         .
	*/
	updateGui() {
		for(let l of this.listeners)
			l();
		this.gui.updateDisplay();
	}
	/**
		  ,  ,     .
		    :
			1.     options
			2.     dat.gui
			3.  -  ,            .
	*/
	setupOptions(){
		const options = {
			"":1,
			"":1,
			"":1,
			
			"X":0,
			"Y":0,
			"Z":0,
			
			"": 0xFFFFFF,
			"": true,

		};
		this.options = options;
		
		let self = this;

		
		this.gui.add(options, '',0.1, 10, 0.1).onChange((e)=>{
			self.stage.setScale(self.stage.selectedObject, options[""], options[""], options[""] );
		});
		this.gui.add(options, '',0.1, 10, 0.1).onChange((e)=>{
			self.stage.setScale(self.stage.selectedObject, options[""], options[""], options[""] );
		});
		this.gui.add(options, '',0.1, 10, 0.1).onChange((e)=>{
			self.stage.setScale(self.stage.selectedObject, options[""], options[""], options[""] );
		});
		this.gui.add(options, 'X',0, Math.PI*2, Math.PI/16).onChange((e)=>{
			self.stage.setRotation(self.stage.selectedObject, options["X"], options["Y"], options["Z"]);
		});
		this.gui.add(options, 'Y',0, Math.PI*2, Math.PI/16).onChange((e)=>{
			self.stage.setRotation(self.stage.selectedObject, options["X"], options["Y"], options["Z"]);
		});
		this.gui.add(options, 'Z',0, Math.PI*2, Math.PI/16).onChange((e)=>{
			self.stage.setRotation(self.stage.selectedObject, options["X"], options["Y"], options["Z"]);
		});
		this.gui.add(options, '').onChange( (e)=>{
			for(let o of self.stage.movableObjects) {
				if (o.name === 'container') {
					for(let c of o.children){
						if (c.isLine || c.userData.isText) {
							c.visible = e;
						}
					}
				}
			}
			this.stage.controller.labelManager.setDimensionsVisibility(e);
		});
		this.gui.addColor(options, '').onChange((e)=>{
			self.stage.setMeshColor(self.stage.selectedObject, e);
		});
	
		
		this.listeners.push(()=>{
			if(!self.stage.selectedObject || self.stage.selectedObject.name !== "container") return;
			
			let models = self.stage.selectedObject.children.filter( (c) => {return c.name === 'models'});
			
			self.options[""] = models[0].scale.x;
			self.options[""] = models[0].scale.y;
			self.options[""] = models[0].scale.z;
			
			self.options["X"] = self.stage.selectedObject.rotation.x;
			self.options["Y"] = self.stage.selectedObject.rotation.y;
			self.options["Z"] = self.stage.selectedObject.rotation.z;
			
			if(self.stage.selectedObject.isMesh) self.options[""] = self.stage.selectedObject.material.color.getHex();
			else {
				utils.applyToMeshes(
					self.stage.selectedObject,
					(o)=>{
						utils.applyToArrayOrValue(
							o.material,
							(m)=>{
								self.options[""] = m.color.getHex();
							}
						);
					}
				)
			}
		})
	}
	
	/**
		 dat.GUI 
	*/
	hide(){
		this.gui.hide();
	}
	/**
		 dat.GUI .
	*/
	show(){
		this.gui.show();
	}
	
	
	
	//  ,   
	//    -   .
	// 		,   - ,  ...args  ,        .
	// addNumOptions(options, min, max, step, onchange, ...args) {
		// for(let [key,v] of Object.entries(options))
			// this.gui.add(options, key, min, max, step).onChange( (e)=>{
				
				// onchange(args, e); 
			// });
	// }
	// addColorOptions(options, onchange, ...args) {
		// for(let [key,v] of Object.entries(options))
			// this.gui.add(options, key).onChange( (e) => { onchange(args, e) });
	// }
	
	
}